// const { defineDocumentType, makeSource } = require ('contentlayer/source-files')
import { defineDocumentType, makeSource } from "contentlayer/source-files";

const computedFields = {
  slug: {
    type: "string",
    resolve: (doc) => doc._raw.sourceFileName.replace(/\.md/, ""),
  },
};

const Post = defineDocumentType(() => ({
    name: 'Post',
    filePathPattern: `**/*.md`,
    bodyType: 'markdown',
    fields: {
      title: {
        type: 'string',required: true,
      },
      date: {
        type: 'date',
        required: true,
      },
      author:{
        type: 'string',
        required: true,

      },
      categories:{
        type: 'list',
        required: true,

      },
      description:{
        type: 'string',
        required: true,

      },
      image:{
        type: 'string',
        required: true,
      },
      images:{
        type: 'list',
        required: true,
      },
      summary:{
        type: 'string',
        required: true,
      },
      tags:{
        type: 'list',
        required: true,

      }

    },
    computedFields,
  
  }))
  
  export default makeSource({
    contentDirPath: 'posts',
    documentTypes: [Post],
  })